.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.App-main {
  flex: 1;
  padding: 20px;
  background-color: #1e1e1e;
  position: relative;
}

.main-content {
  transition: width 0.3s ease-in-out;
  width: 100%;
}

.main-content.with-detail-panel {
  width: calc(100% - 400px);
}

.layout {
  height: calc(100vh - 100px);
}

.editor-section {
  background-color: #1e1e1e;
  border: 1px solid #333;
  border-radius: 4px;
  padding: 10px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.editor-section h2 {
  margin: 0 0 10px 0;
  color: #fff;
  font-size: 1.2em;
}

.editor-section .monaco-editor {
  flex: 1;
  height: 100% !important;
}

.table-container {
  margin-bottom: 10px;
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: #1e1e1e;
  color: #fff;
}

th, td {
  padding: 8px;
  text-align: left;
  border: 1px solid #333;
}

th {
  background-color: #2d2d2d;
}

tr:nth-child(even) {
  background-color: #252525;
}

tr:hover {
  background-color: #2a2d2e;
  transition: background-color 0.2s ease;
}

/* Grid layout specific styles */
.react-grid-item {
  transition: all 200ms ease;
  transition-property: left, top;
}

.react-grid-item.cssTransforms {
  transition-property: transform;
}

.react-grid-item.resizing {
  z-index: 1;
  will-change: width, height;
}

.react-grid-item.react-draggable-dragging {
  transition: none;
  z-index: 3;
  will-change: transform;
}

.react-grid-item.react-grid-placeholder {
  background: #333;
  opacity: 0.2;
  transition-duration: 100ms;
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.react-grid-item > .react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  cursor: se-resize;
}

.react-grid-item > .react-resizable-handle::after {
  content: "";
  position: absolute;
  right: 3px;
  bottom: 3px;
  width: 5px;
  height: 5px;
  border-right: 2px solid rgba(255, 255, 255, 0.4);
  border-bottom: 2px solid rgba(255, 255, 255, 0.4);
}

h1 {
  margin: 0;
  font-size: 1.5rem;
}

.detail-panel {
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 400px;
  background-color: #1e1e1e;
  border-left: 1px solid #333;
  z-index: 1000;
  display: flex;
  flex-direction: column;
}

.detail-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #333;
  background-color: #252526;
}

.detail-panel-header h2 {
  margin: 0;
  font-size: 1.2em;
  color: #fff;
}

.close-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  padding: 0 10px;
  line-height: 1;
}

.close-button:hover {
  color: #ccc;
} 
